class Controler extends EventTarget {
    constructor(){
        super();
        this.currentUser="";
    }

    user(user){
        this.dispatchEvent(
            new CustomEvent(
                "currentUser",
                {detail: {user: user}}
                )
        );
        localStorage["user"] = JSON.stringify(user)
        this.currentUser=user
        console.log(this.currentUser)
    }
}

module.exports = Controler;